export default {
	name: 'userInfoBtn',
	data() {
		return {}
	},
	computed: {
		userInfo() {
			return this.$store.state.user.userInfo
		}
	},
	methods: {
		mpGetUserInfo(e) {
			if (e.detail.errMsg !== 'getUserInfo:ok') {
				wx.showToast({
					title: '取消授权',
					icon: 'none',
				})
				return;
			}
			const { nickName: NickName, avatarUrl: Avatar } = e.detail.userInfo
			this.$store.commit('setUserInfo', { NickName, Avatar })
		},
	}
}
